import React from 'react';
import ProfileInfo, {ProspProfileInfoType} from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import ProfileImg from "./ProfileImg/ProfileImg";
import {makeStyles} from '@mui/styles';
import {Theme} from '@mui/material';

const useStyles = makeStyles((theme: Theme) => ({
    wrapper_profile: {
        display: 'grid',
        gridTemplateRows: "1fr auto",
        gridTemplateColumns: '4fr 8fr',
        gridGap: '1rem',
    },
    wrapperProfileImg: {
        background: '#ffffff',
        borderRadius: '6px'
    },
    wrapperProfileInfo: {
        background: '#ffffff',
        borderRadius: '6px',
        padding: '15px'
    },
    wrapperMyPostsContainer: {
        background: '#ffffff',
        borderRadius: '6px',
    },
}))

const Profile: React.FC<ProspProfileInfoType> = (props) => {
    const classes = useStyles();
    return (

        <div className={classes.wrapper_profile}>
            <div className={classes.wrapperProfileImg}>
                <ProfileImg profile={props.profile}
                            isOwner={props.isOwner}
                            savePhoto={props.savePhoto}
                />
            </div>
            <div className={classes.wrapperProfileInfo}>
                <ProfileInfo profile={props.profile}
                             status={props.status}
                             isOwner={props.isOwner}
                             updateStatus={props.updateStatus}
                             saveProfile={props.saveProfile}
                             savePhoto={props.savePhoto}
                />
            </div>
            <div></div>
            <div className={classes.wrapperMyPostsContainer}>
                <MyPostsContainer/>
            </div>
        </div>
    );
}

export default Profile