import React from 'react';
import Post from "./Post/Post";
import {Field, Form} from "react-final-form";
import {required} from "../../../utils/validators/validators";
import {PostType} from "../../../types/type";
import {makeStyles} from "@mui/styles";
import {Button, Theme, Typography} from "@mui/material";
import SendIcon from "@mui/icons-material/Send";

const useStyles = makeStyles((theme: Theme) => ({
    profileMyPost: {
        margin: "25px"
    },
    myPostContainer: {
        display: 'flex',
        alignItems: 'center',
    },
    myPostButton: {
        padding: "12px"
    }
}))


type AddNewPostFormPropsType = {
    onSubmit: (values: any) => void
}
const AddNewPostForm: React.FC<AddNewPostFormPropsType> = (props) => {
    const classes = useStyles();

    return (
        <div>
            <Form
                onSubmit={props.onSubmit}
                render={({handleSubmit}) => (
                    <form onSubmit={handleSubmit}>
                        <div className={classes.myPostContainer}>
                            <div>
                                <Field name="newPostText"
                                       component='textarea'
                                       validate={required}
                                       placeholder={"Post message"}
                                />
                            </div>
                            <div className={classes.myPostButton}>
                                <Button variant="contained"
                                        color="info"
                                        endIcon={<SendIcon/>}
                                        type="submit" name={"btnAddPost"}
                                >
                                    Send
                                </Button>
                            </div>
                        </div>
                    </form>
                )}
            />
        </div>
    )
}

type InitialMyPostPropsType = {
    PostData: PostType[]
    newPostText: string
    addPost: (newPostText: string) => void
}


const MyPosts: React.FC<InitialMyPostPropsType> = React.memo(props => {
    const classes = useStyles();

    let PostDataNew = props.PostData.map(
        (element) => {
            return (
                <Post key={element.id} message={element.message} value={element.value}/>
            )
        }
    );

    let onAddPost = (formData: any) => {
        console.log(formData)
        props.addPost(formData.newPostText);
        formData.newPostText = "";
    }

    return (
        <div className={classes.profileMyPost}>
            <div>
                <Typography sx={{
                    textAlign: 'center',
                    fontWeight: '600',
                    fontSize: '30px'
                }}>
                    My posts
                </Typography>
                <AddNewPostForm onSubmit={onAddPost}/>
            </div>
            <div>{PostDataNew}</div>
        </div>
    )
})


export default MyPosts