import React, {useState} from 'react';
import Preloader from "../../common/Preloader/Preloader";
import {ProfileDataForm} from "./ProfileDataForm";
import {ContactsType, ProfileType} from '../../../types/type';
import ProfileStatus from '../ProfileStatus/ProfileStatus';
import {makeStyles} from '@mui/styles';
import {Button, Theme} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';

const useStyles = makeStyles((theme: Theme) => ({
    profileLoginName: {
        fontWeight: 700,
        fontSize: '30px',
    },
    ProfileDataDiv: {
        marginTop: "15px",
        fontSize: '19px',
        color: '#2a5885',
    },
    ProfileDataKey: {
        color: '#818e9c',
        paddingRight: "10px",
        fontWeight: 600,

    },
    ProfileDataKeyContacts: {
        padding: "0px 10px 0px 15px",
        color: '#818e9c',
        fontWeight: 600,
    },
    ProfileDataKeysDiv: {
        padding: "3px 0px",
    },
    goEditModButton: {
        padding: "3px 0px",
        display: 'flex',
        justifyContent: 'center'
    }
}))

export type ProspProfileInfoType = {
    profile: ProfileType
    isOwner: boolean
    status: string
    updateStatus: (status: string) => void
    saveProfile: (profile: ProfileType) => Promise<any>
    savePhoto: (file: File) => void
}


const ProfileInfo: React.FC<ProspProfileInfoType> = (props) => {
    const classes = useStyles()

    let [editMode, setEditMode] = useState(false)

    if (!props.profile) {
        return <Preloader/>
    }

    const onSubmit = (formData: ProfileType) => {
        props.saveProfile(formData)
        setEditMode(false)
    }

    return (
        <div>
            <div>
                <span className={classes.profileLoginName}>
                    {props.profile.fullName}
                </span>
                <ProfileStatus status={props.status}
                               updateStatus={props.updateStatus}
                               isOwner={props.isOwner}
                />
                {editMode
                    ? <ProfileDataForm profile={props.profile}
                                       initialValues={props.profile}
                                       isOwner={props.isOwner}
                                       onSubmit={onSubmit}
                                       goToEditMode={() => {
                                           setEditMode(false)
                                       }}/>
                    : <ProfileData profile={props.profile}
                                   isOwner={props.isOwner}
                                   goToEditMode={() => {
                                       setEditMode(true)
                                   }}/>}

            </div>
        </div>
    );
}
type InitialProspProfileDataType = {
    profile: ProfileType
    isOwner: boolean
    goToEditMode: () => void
}
const ProfileData: React.FC<InitialProspProfileDataType> = (props) => {
    const classes = useStyles()

    return <div className={classes.ProfileDataDiv}>
        <div className={classes.ProfileDataKeysDiv}>
            <span className={classes.ProfileDataKey}>Name:</span> {props.profile.fullName}
        </div>
        <div className={classes.ProfileDataKeysDiv}>
            <span className={classes.ProfileDataKey}>About me:</span> {props.profile.aboutMe}
        </div>
        <div className={classes.ProfileDataKeysDiv}>
            <span className={classes.ProfileDataKey}>My professional skills:</span>
            {props.profile.lookingForAJobDescription}
        </div>
        <div className={classes.ProfileDataKeysDiv}>
            <span className={classes.ProfileDataKey}>Looking for a job:</span>
            {props.profile.lookingForAJob ? "yes" : "no"}
        </div>
        <div className={classes.ProfileDataKeysDiv}>
            <span className={classes.ProfileDataKey}>Contacts:</span>
            {Object
                .keys(props.profile.contacts)
                .map((key)  => {
                    return <Contact key={key}
                                    contactTitle={key}
                                    contactValue={props.profile.contacts[key as keyof ContactsType]}
                    />
            })}
        </div>
        <div className={classes.goEditModButton}>
            {props.isOwner
                ? <Button variant="contained"
                          color="info"
                          onClick={props.goToEditMode}
                          endIcon={<SendIcon/>}>
                    Edit
                </Button>
                : <span></span>
            }
        </div>
    </div>
}

type InitialPropsContactType = {
    contactTitle: string
    contactValue: string
}
export const Contact: React.FC<InitialPropsContactType> = ({contactTitle, contactValue}) => {
    const classes = useStyles()
    return <div className={classes.ProfileDataKeysDiv}>
        <span className={classes.ProfileDataKeyContacts}>
            {contactTitle}:
        </span>
        <a href={contactValue}>{contactValue}</a>
    </div>
}


export default ProfileInfo
